import React from 'react';
import { Stack, Typography } from '@mui/material';

import { translate } from '../../../common/translate/translate';
import { useNormilizeTime } from '../../../hooks/useNormalizeTime';

type FilmsItemInfoProps = {
    keyName: string;
    value: number;
};


export const FilmsItemInfo: React.FC<FilmsItemInfoProps> = ({ keyName, value }) => {
    return (
        <Stack flexDirection='row' sx={{ mt: 1 }}>
            <Typography variant='body2'>{translate(keyName)}</Typography>
            <Typography variant='body2' sx={{ padding: '0 4px 0 1px' }}>:</Typography>
            {keyName !== 'filmLength' ?
                <Typography variant='body2'> {value}</Typography>
                :
                <Typography variant='body2'> {useNormilizeTime(value)}</Typography>
            }
        </Stack>
    );
};

